}

input {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 10px;
}

.inputBox {
  width: 250px;
  position: relative;
}

.inputBox #search-box {
  width: 100%;
  background: #eeeef2;
  border: 1px solid #252525;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 1em;
}

.inputBox #search-box:focus {
  border: 2px solid #2b57b8;
}

.inputBox span {
  pointer-events: none;
  text-transform: uppercase;
  color: #b6b6b6;
  padding: 10px;
  font-size: 1em;
  transition: all .5s;
  position: absolute;
  left: 10px;
}

.inputBox input:valid ~ span, .inputBox input:valid ~ span {
  color: #2b57b8;
  transform: translateX(10px), translateY(-10px);
  background: #eeeef2;
  padding: 0 10px;
  font-size: .65em;
  top: -10px;
}

